import React, { Component } from 'react'
import { Card, Avatar } from 'antd'
import { withRouter } from 'react-router-dom'
const { Meta } = Card

class CardClub extends Component {

    renderList(list) {
        return list.map((item, i) => {
            let { tenDoiBong, logoDoiBong, stadiumImg, sanNha, namThanhLap, tenHLV, _id } = item
            return (
                <Card
                    hoverable
                    cover={<img alt="example"
                        src={stadiumImg}
                    />}
                    style={{ display: "inline-block", margin: "10px", width: "250px" }}
                    onClick={() => this.props.history.push(`/club-detail?id=${_id}`)}
                >
                    <Avatar size={100} src={logoDoiBong} style={{ position: "relative", marginLeft: "45px", marginTop: "-50px" }} />
                    <Meta title={tenDoiBong} description={sanNha} style={{ textAlign: "center", marginTop: "10px" }} />
                </Card>
            )
        })
    }

    render() {
        let list = this.props.list || []

        return (
            <div>
                {this.renderList(list)}
            </div>

        )
    }
}

export default withRouter(CardClub)