import React, { Component } from 'react'
import axios from 'axios'
import CardClub from '../../pieces/cardclub'

class ClubList extends Component {
    constructor(props) {
        super(props)

        this.state = {
            requesting: false,
            listInQueue: false,
            list: [],
            teamInfo: ''
        }
        this.callApiList = this.callApiList.bind(this)
    }

    componentDidMount() {
        let state = this.state
        this.callApiList(state)

    }

    callApiList(state) {
        if (this.state.requesting) {
            this.setState({ listInQueue: true })
            return
        }

        axios.request({
            url: 'http://localhost:1900/api/team',
            method: 'GET'
        }).then(res => {
            let item = res.data
            this.setState({ list: item })
            console.log(item)
        })
    }

    render() {
        let { list } = this.state

        return list ? (
            <div>
                <CardClub list={list} />
            </div>
        ) : (
                <div>
                    Not have anything in this page
                </div>
            )
    }
}

export default ClubList