import React, { Component } from 'react'
import '../../App.css'
import Header from '../../components/parts/Header'
import Footer from '../../components/parts/Footer';
import MiniTables from '../../components/pieces/LeftContent'
import RightContent from '../../components/pieces/RightContent'
// import MiniFixture from '../../components/pieces/MiniFixture'
import RightSider from '../../components/pieces/RightSider'

// import Button from '../../components/pieces/Button'

class Home extends Component {
    constructor(props) {
        super(props)
        const user = JSON.parse(localStorage.getItem('user')) || null
        this.state = {
            user: user
        }
    }
    render() {
        return (
            <div>
                <Header />
                <div style={{ backgroundColor: "#3ce494", backgroundImage: "url(https://www.premierleague.com/resources/prod/3cfdf38-2800/i/bg-elements/heading-bg-desktop.svg)" }}>
                    <div className="container">
                        <div className="row">
                            <div className="col col-lg-3">
                                <MiniTables />
                            </div>
                            <div className="col">
                                <RightSider />
                            </div>
                        </div>
                    </div>
                </div>

                <div style={{ marginTop: "10px" }}>
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <RightContent />
                            </div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}

export default Home