import React, { Component } from 'react'
import CTitle from '../../components/pieces/Title'
import CSpacer from '../../components/pieces/Spacer'
import Axios from 'axios'
import Header from '../../components/parts/Header'
import Footer from '../../components/parts/Footer'
import { Card } from 'antd'

class ClubDetail extends Component {
    constructor(props) {
        super(props)

        this.state = {
            clubId: null,
            clubInfo: null
        }
    }

    componentDidMount() {
        let clubId = new URLSearchParams(window.location.search).get('id')
        this.setState({
            clubId
        })

        Axios.request({
            url: `http://localhost:1900/api/team/${clubId}`,
            method: 'GET'
        }).then(res => {
            let item = res.data
            this.setState({ clubInfo: item })
            console.log("ITEMMMMMMMMMM", item)
        })
    }

    render() {
        let { clubInfo } = this.state
        console.log(clubInfo)
        return clubInfo ?
            (
                <div>
                    <Header></Header>
                    <div className="container">
                        <CTitle>
                            <img src={clubInfo.logoDoiBong}
                                style={{ width: '300px', position: 'relative', marginBottom: '-50px', paddingLeft: '40px', marginLeft: '-120px', marginTop: '-40px' }}></img>
                            <span style={{ paddingLeft: '40px' }}>{clubInfo.tenDoiBong}</span>
                        </CTitle>
                        <CSpacer />
                        <div style={{ paddingTop: '20px' }}>
                            <div><b>{clubInfo.tenDoiBong}</b> is <span>{clubInfo.description}</span></div>
                            <div>
                                <Card
                                    style={{
                                        width: 340,
                                        margin: 'auto',
                                        // marginTop: 64,
                                        padding: "10px",
                                        textAlign: "center"
                                    }}
                                    bordered={true}>
                                    <h3>Overview</h3><hr />
                                    <div style={{ fontSize: '15px', display: 'flex', justifyContent: `space-between` }}>
                                        <span>Head Coach </span>
                                        <span> {clubInfo.tenHLV}</span> </div><hr />
                                    <div style={{ fontSize: '15px', display: 'flex', justifyContent: `space-between` }}>
                                        <span>Born Year </span>
                                        <span> {clubInfo.namThanhLap}</span> </div>
                                </Card>
                            </div>
                        </div>
                    </div>
                    <Footer />
                </div>
            )
            :
            (
                <div>
                    <Header />
                    < div className="container" >
                        <CTitle> Not have any club with this id</CTitle>
                        <CSpacer />
                    </div >
                    <Footer />
                </div >
            )
    }
}

export default ClubDetail